const Refs = () => {



    return (
        <>
            <div className="text-blok">
                <h1>Refs, useRef</h1>
                <p><strong>Refs</strong> (  )     DOM-  React-,    <strong>render()</strong>.</p>
                <ul>     :
                    <li> ,     .</li>
                    <li>    .</li>
                    <li>   ,   DOM.</li>
                </ul>
                <p>    <strong>React.createRef()</strong>    React-    <strong>ref</strong>.     ,     ,         .</p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`  class MyComponent extends React.Component {
                constructor(props) {
                  super(props);
                  this.myRef = React.createRef();
                }
                render() {
                  return <div ref={this.myRef} />;
                }
            }`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p>     <strong>useRef</strong>:</p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`import { useRef } from 'react';

function MyComponent () {
    const refContainer = useRef(initialValue);
    return (
        <div ref={refContainer} />
    );
}` 
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <ul>
                    <li> <strong>initialValue</strong>:    ref  current.      .      .</li>
                    <li>useRef      <strong>current</strong>. ( initialValue,   )     ref  React   ref    JSX, React   current .    useRef    .</li>
                </ul>
                <p>    useRef:</p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`import React, { useEffect, useState, Fragment, useRef } from 'react'

const Test = () => {
    const [text, setText] = useState('')
    const textInput = useRef(null)
    const valid = () => {
        textInput.current.focus()
    }
    return (
        <div>
            <input ref={textInput} value={text} />
            <button onClick={() => valid()}>
                Click to focus
            </button>
        </div>
    )
}

export default Test`   
                        }</code>
                </pre>
            </div>
        </>
    )
}
export default Refs;