
const ProfilerComponent = () =>{

    return (
        <>
            <div className="text-blok">
                <h1><strong>Profiler</strong></h1>
                <p><strong>React.Profiler </strong>  ,   React,      .        ,        .      ,     .</p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`import React, { Profiler } from 'react';`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p> Profiler    : <strong>id</strong>  <strong>onRenderCallback</strong>. </p>
                <p> <strong>id</strong>     ,  <strong>onRenderCallback</strong>     ,          .</p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`const onRenderCallback = (
  id, // ID  ,    
  phase, // "mount" (  )  "update" (  )
  actualDuration, // ,     
  baseDuration, // ,        
  startTime, //   
  commitTime, //   
  interactions //  ""  
) => {
  console.log({
    id,
    phase,
    actualDuration,
    baseDuration,
    startTime,
    commitTime,
    interactions,
  });
};

const App = () => {
  return (
    <Profiler id="App" onRenderCallback={onRenderCallback}>
      <div>
        <h1>App with Profiler</h1>
        <MyComponent />
      </div>
    </Profiler>
  );
};

export default App;`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <ul>     ,      :
                    <li><strong>  React.memo</strong></li>
                    <li><strong>  useMemo  useCallback</strong></li>
                    <li><strong> , React.lazy  Suspense</strong></li>
                </ul>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`//React.memo
import React, { memo } from 'react';

const MyComponent = memo(() => {
  //  
});


//useMemo, useCallback
import React, { useMemo, useCallback } from 'react';

const MyComponent = ({ data }) => {
  const computedValue = useMemo(() => {
    //   
    return expensiveComputation(data);
  }, [data]);

  const handleClick = useCallback(() => {
    //  
  }, []);

  return <div onClick={handleClick}>{computedValue}</div>;
};

//React.lazy  Suspense
import React, { Suspense, lazy } from 'react';

const LazyComponent = lazy(() => import('./LazyComponent'));

const App = () => {
  return (
    <Suspense fallback={<div>Loading...</div>}>
      <LazyComponent />
    </Suspense>
  );
};`
                        }</code>
                </pre>
            </div>
        </>
    )
}
export default ProfilerComponent;