
const UseParams = () => {

    return (
        <>
            <div className='text-blok'>
                <p><strong>useParams</strong></p>
                <p><strong>useParams</strong> -  ,   <strong>   </strong>  React Router Dom v6.</p>
                <p>  params,       </p>
            </div>
            <div className='code-blok'>
                <pre>
                    <code>{
                        `//App.js
const users = [
    { id: 1, name: 'Alice' },
    { id: 2, name: 'Bob' },
    { id: 3, name: 'Charlie' },
  ];
return(
    <>
        <Routes>
            <Route path="/user/:id" element={<User />} />
            // 
        </Routes>
        <h1> </h1>
      <ul>
        {users.map(user => (
          <li key={user.id}>
            <Link to={'/user/{user.id}'}>{user.name}</Link>
          </li>
        ))}
      </ul>
//  Link  useNavigate
// 
const navigate = useNavigate();
const handleClick =( id)=>{
  navigate('/user/{id}')//    
}
// return 
<h1> </h1>
      <ul>
        {users.map(user => (
          <li key={user.id}>
            <button onClick={()=> handleClick(user.id)}>{user.name}</button>
          </li>
        ))}
      </ul>
      
    </>
)

//User.js
import { useParams } from 'react-router-dom';

const User = () => {
  const { id } = useParams(); //   id  URL

  return (
    <div>
      <h1> </h1>
      <p>ID : {id}</p>
    </div>
  );
};

export default User;`
                    }</code>
                </pre>
            </div>
        </>
    )
}
export default UseParams;