
const UseMemo = () => {


    return (
        <>
            <div className="text-blok">
                <h1>useMemo</h1>
                <p>React  <strong>useMemo</strong>   <strong></strong>    .   <strong></strong>    <strong> </strong> ,    () <strong> </strong>.     ,   ,        .</p>
                <p>React.memo     ,  useMemo    ,     ,      return.</p>
                <ul> :
                    <li>  : ,     , ,     </li>
                    <li> </li>
                </ul>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`import { useMemo } from 'react';

const ExpensiveComponent = ({ propA, propB }) => {
  //  ,   
  return (
    // ...
  );
};

const ParentComponent = ({ data }) => {
    const processedData = useMemo(() => {
        //   
        return processData(data);
    }, [data]); //  -   "data"

    const cachedComponent = useMemo(() => {
    //       propA  propB
    return <ExpensiveComponent propA={propA} propB={propB} />;
    }, [propA, propB]);

    return (
        <div>
            <p>{processedData}</p>
            <p>{cachedComponent}</p>
        </div>
  );
};`   
                        }</code>
                </pre>
            </div>
        </>
    )
}

export default UseMemo;