import { useState } from 'react';
import './events.css';

const Events =() => {
    const [state, setState] = useState(false);
    const handleClick = () => {
      console.log('!');
      setState(!state)
    }


    return (
        <>
            <div className="text-blok">
                <h1> </h1>
                <p>   ,   :</p>
                <ul>
                    <li>onClick ()</li>
                    <li>onChange ()</li>
                    <li>onSubmit ( )</li>
                    <li>onMouseOver ( )</li>
                    <li>onKeyDown ( )</li>
                    <li>onKeyUp(,    )</li>
                    <li>onFocus(       .)</li>
                    <li>onBlur(    )</li>
                    <li>onScroll(  ) </li>
                    <li>onWheel( )</li>
                </ul>
                <p>  onClick:</p>
            </div>
            <button className='button-events' onClick={handleClick}>{state ? '' : ' '}</button>
            <div className='code-blok'>
                <pre>
                    <code>{
`import { useState } from 'react';

const Event =() => {
    const [state, setState] = useState(false);
    const handleClick = () => {
      setState(!state)
    }
    return (
        <div>
            <button className='button-events' 
            onClick={handleClick}>{state ? '' : ' '}</button>
        </div>
    )
}`
                        }</code>
                </pre>
            </div>
        <div className='text-blok'>
            <p>    onKeyDown:</p>
        </div>
        <div className='code-blok'>
            <pre>
                <code>{
`const MyComponent = () => {
    const handleKeyDown = (event) => {
  console.log(' :', event.key);
}
  return (
    <input type="text" onKeyDown={handleKeyDown} />
  );
}`
                    }</code>
            </pre>
        </div>
        </>
    )
}
export default Events;