
const HOC = () =>{


    return(
        <>
            <div className="text-blok">
                <h1><strong>HO (Higher-Order Components)</strong></h1>
                <p><strong>HOC</strong>     ,          . </p>
                <p>    ,    ,   ,          <strong>   </strong>.</p>
                <ul>HOC      React-:
                    <li><strong> </strong>: HOC     ,       ,         .</li>
                    <li><strong>  props</strong>: HOC     props .  , ,             .</li>
                    <li><strong> </strong>: HOC        , ,  ,     ..</li>
                </ul>
                <p><strong> HOC:</strong></p>
                <p>1.  ,      .</p>
                <p>2.     ,          .</p>
                <p>3.    .</p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`const withLogger = (WrappedComponent) => {
  return (props) => {
    console.log('Component rendered:', WrappedComponent.name);
    const extraProps = { additionalProp: "I'm extra!" };
    //  
    return <WrappedComponent {...props} {...extraProps} />;
  };
};`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p> HOC:</p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`//  HOC   
// App.js
const CounterWithHOC = withCounter(Counter);
//   Counter    
//   

// ,     HOC
const App = () => {
  return (
    <div>
      <h1>Counter App</h1>
      <CounterWithHOC />
    </div>
  );
};`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p><strong>  </strong></p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`// HOC
const withCounter = (WrappedComponent) => {
  return (props) => {
    const [count, setCount] = useState(0);

    const incrementCount = () => {
      setCount(count + 1);
    };

    return (
      <WrappedComponent
        count={count}
        incrementCount={incrementCount}
        {...props}
      />
    );
  };
};

// ,    HOC
const Counter = ({ count, incrementCount }) => {
  return (
    <div>
      <p>Count: {count}</p>
      <button onClick={incrementCount}>Increment</button>
    </div>
  );
};`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p><strong> </strong></p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`import React, { useEffect } from 'react';

const withLogger = (WrappedComponent) => {
  return (props) => {
    useEffect(() => {
      console.log('Component {WrappedComponent.name} mounted.');
    }, []);

    return <WrappedComponent {...props} />;
  };
};`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p><strong></strong></p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`const withAuth = (WrappedComponent) => {
  return (props) => {
    const isAuthenticated = checkAuthStatus(); // ,    .
    if (isAuthenticated) {
      return <WrappedComponent {...props} />;
    } else {
      return <p>Please log in to access this content.</p>;
    }
  };
};

// App.js
const AuthenticatedComponent = withAuth(ProfileComponent);`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p><strong></strong></p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`const withLocalization = (WrappedComponent) => {
  return (props) => {
    const locale = getCurrentLocale(); //   .
    const localizedContent = getLocalizedContent(locale);
    return <WrappedComponent {...props} localizedContent={localizedContent} />;
  };
};

// App.js
const LocalizedProfile = withLocalization(ProfileComponent);`
                        }</code>
                </pre>
            </div>
        </>
    )
}
export default HOC;
