import {  Outlet, NavLink } from 'react-router-dom';

const LazyComponent = ()=>{

    
    return(
        <>
            <div className="text-blok">
                <h1><strong>Lazy</strong></h1>
                <p>         ,  <strong>" " (lazy loading)</strong> .</p>
                <p><strong>lazy</strong>     ,    .     , lazy      <strong>    </strong>. </p>
                <ul><strong>   lazy:</strong>
                    <li>1. <strong>   (Route-based splitting):</strong>  lazy        (routes)  .         .</li>
                    <li>2. <strong>   (Component-based splitting):</strong>    ,      , ,     .</li>
                    <li>3. <strong> :</strong>    ,    .        ,    .</li>
                    <li>4. <strong> :</strong>      fallback  <strong>Suspense</strong>,   ,   .        .</li>
                    <li>5. <strong> :</strong>     ,    .     <NavLink to="errorBoundary"  className={({ isActive }) => (isActive ? 'active' : 'router-item')}>ErrorBoundary</NavLink>  .</li>
                </ul>
                
                <Outlet />
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`import React, { Suspense, lazy } from 'react'; // React.lazy  
//  
const LazyComponent = lazy(() => import('./DogImage'));

const App = () => {
  return (
    <div>
      <h1>Random Dog Image</h1>
        <ErrorBoundary>
        //Suspense     (,  ), 
        //  ,    :
          <Suspense fallback={<div>Loading...</div>}>
            <LazyComponent  />
          </Suspense>
      </ErrorBoundary>
    </div>
    
  );
};

//DogImage
// DogImage.jsx
import React, { useState, useEffect } from 'react';

const DogImage = () => {
  const [data, setData] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('https://dog.ceo/api/breeds/image/random');
        const result = await response.json();
        setData(result);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
  }, []);

  if (!data) {
    return null; // Suspense fallback will handle the loading state
  }

  return <img src={data.message} alt="Random Dog" />;
};

export default DogImage;`
                        }</code>
                </pre>
            </div>
            <div className='text-blok'>
              <ul> React.lazy:
                <li>      (default exports)</li>
                <li>   React-.         JavaScript     .
                </li>
                <li>  Suspense.</li>
                <li>  , React.lazy  Suspense     (SSR).  ,               HTML  .</li>
                <li>      (,        ), React.lazy        .      ErrorBoundary     .</li>
                <li>  .</li>
              </ul>
            </div>
        </>
    )
}
export default LazyComponent;