import { useNavigate} from 'react-router-dom';


const UseNavigate = () =>{
    const navigate = useNavigate();
    const handleClick = () => {
        navigate('/');
    };

    return (
        <>
            <div className="text-blok">
                <p><strong>useNavigate</strong></p>
                <p><strong>useNavigate</strong> -  ,         React Router Dom v6.</p>
                <p>   useNavigate,     <strong>navigate</strong>         . </p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
                        `import { useNavigate } from 'react-router-dom';

// ...

const Home = () => {
  const navigate = useNavigate();
    <Routes>
        <Route path="/about" element={<About />} />
    </Routes>

  const handleClick = () => {
    navigate('/about');
  };

  return (
    <div>
      <h1> </h1>
      <button onClick={handleClick}>    </button>
    </div>
  );
};`
                    }</code>
                </pre>
            </div>
            <div className="text-blok">
                <ul>   :
                    <ul>useNavigate
                        <li>           (,  ,     ..).</li>
                        <li>     .</li>
                    </ul>
                    <ul>Link
                        <li>  ,        .</li>
                        <li>      .</li>
                    </ul>
                </ul>
                <p>  ,    Link  useNavigate  ,      <strong>Route</strong>  . </p>
                <button onClick={handleClick}>   </button>
                
            </div>
        </>
    )
}
export default UseNavigate;