const FormComponent = () => {


    return (
        <>
            <div className="text-blok">
                <h1>  </h1>
                <p>      React Hook Form  Formik</p>
                <p>React Hook Form:</p>
                <p><strong>  React Hook Form:</strong></p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`npm install react-hook-form`
                    }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p><strong>    </strong></p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`import { useForm } from 'react-hook-form'

const FormWithRHF = () => {
    const { register, handleSubmit, watch, formState: { errors } } = useForm();`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p><strong>    </strong></p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`//App.js
return (
    <>
        <form onSubmit={handleSubmit(onSubmit)}>
            <label>:</label>
            <input {...register('name')} />
            ..
        </form>
    </>`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p><strong></strong></p>
                <p><strong>register</strong> -     react-hook-form,              .</p>
                <p><strong>register('fieldName', validationRules)</strong>-  , <strong>    fieldName</strong>,   <strong>  (validationRules)</strong>   .</p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`<input {...register('email', { required: ' email' })} />
<p>{errors.email?.message}</p>`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p>   ,   email <strong>(required)</strong>  .     ,    " email".</p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`pattern: {
    value: /^[--A-Za-z0-9]+$/,
    message: ' ',
},
    
minLength: {
    value: 6,
    message: ' ,   6 '
},

`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p>  </p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`<label> </label>
            <div>
                <label>
                    <input type="radio" {...register('gender', 
                        { required: ' ' })} value="male" />
                    
                </label>
                <label>
                    <input type="radio" {...register('gender', 
                        { required: ' ' })} value="female" />
                    
                </label>
            </div>`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p>    </p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`const FormWithRHF = () => {
    const {.. watch, ..} = useForm();
    const password = watch('password'); //    

//return
<label> </label>
            <input {...register('repeatPassword', {
                required: ' ',
                validate: value =>
                    value === password || '  '
            })} />
            <p>{errors.repeatPassword?.message}</p>`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p><strong>     ""</strong></p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`//App.js ( ) 
const onSubmit = (data) => {
  console.log(data);
};
..
return(
//    
..
<button type="submit"></button>
)
`
                        }</code>
                </pre>
            </div>
        </>
    )
}
export default FormComponent;