import {  useLocation } from 'react-router-dom';

const UseLocation =()=>{
    const location = useLocation();

    return (
        <>
            <div className='text-blok'>
                <p><strong>useLocation</strong></p>
                <p><strong>useLocation</strong> -  ,   <strong>    URL</strong>   <strong></strong>  React Router Dom v6.</p>
                <p>  location,      URL. </p>
            </div>
            <div className='code-blok'>
                <pre>
                    <code>{
`import { useLocation } from 'react-router-dom';

// ...

const Page = () => {
  const location = useLocation();

  return (
    <div>
      <p> : {location.pathname}</p>
      <p> : {location.search}</p>
      <p>: {JSON.stringify(location.state)}</p>
    </div>
  );
};`
                    }</code>
                </pre>
            </div>
            <div className='text-blok'>
                <p> : {location.pathname}</p>
        </div>
        </>
    )
}
export default UseLocation;