

const UseEffect = () => {


    return (
        <>
            <div className="text-blok">
                <h1>    (UseEffect):</h1>
                <p><strong>useEffect</strong> -    React,          .        . </p>
                <ul>
                    <li> : <strong></strong>,          .</li>
                    <li> : <strong> </strong> ,      useEffect.</li>
                </ul>
                <p>   , useEffect      .      ,  useEffect    ,    .</p>
                <p>1. <strong>Mounting ()</strong>:</p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
                        `//   componentDidMount
useEffect(() => { console.log("componentDidMount") }, []) `
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p>2. <strong>Updating ():</strong></p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`//   componentDidUpdate,
//          .
useEffect(() => { console.log("componentDidUpdate") }, [dependency])  `
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p>3. <strong>Unmounting ():</strong></p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`//   componentWillUnmount
//useEffect(() => () => {})   

useEffect(() => {
    return () => console.log('componentWillUnmount')
}, [])`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p>      :</p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`//      componentDidMount  componentDidUpdate
useEffect(() => { console.log("componentDidMount  componentDidUpdate") }) 

//      , 
//        
useEffect(() => {
    console.log('  ');
    return () => {
      console.log('  ');
    };
  }, []);`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p> ,                "componentDidMount",     ,   <strong>   </strong>    "componentDidMount"   .             ,   .</p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`useEffect(() => {
    console.log("componentDidMount");
    async function axiosData(){
        try{
            const response = await axios.get('https://todo-redev.herokuapp.com/api/todos',{
                headers:{
                    'accept': 'application/json',
                    'Authorization': 'Bearer {token}',
                }, 
            });
            console.log(' ', response.data);
        }
        catch(error){
            console.error('   :', error);
        }
    }  
        axiosData();  //  
        return () => {
           console.log('  ');  
        } 
}, [])`
                        }</code>
                </pre>
            </div>
        </>
    )
}
export default UseEffect;