const Lifecycle = () => {



    return (
        <>
            <div className="text-blok">
                <h1>  = LifeCycle</h1>
                <p>   :</p>
                <p>1. <strong>  (Mounting Methods):</strong></p>
                <ul>
                    <li><strong>constructor()</strong>:     .       .</li>
                    <li><strong>render()</strong>:     React .</li>
                    <li><strong>componentDidMount()</strong>:         DOM.      ,       .</li>
                </ul>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`import axios from 'axios';

 class LifecycleComponent extends React.Component{
    constructor (props) {
        super(props)
          this.name = 'Alex'
     }

    async componentDidMount(){
        console.log('didmount');
        try{
             const response = await axios.get('https://todo-redev.herokuapp.com/api/todos',{
                 headers:{
                     'accept': 'application/json',
                     'Authorization': 'Bearer {token}',
                 }, 
             });
             console.log(' ', response.data);
         }
         catch(error){
             console.error('   :', error);
         }

    render(){
         console.log('render');
         return (
             <div>
                 <p>Hello {this.name} </p>
             </div>
         )
       }
 }`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p>2. <strong>  (Updating Methods)</strong>:</p>
                <ul>
                    <li><strong>shouldComponentUpdate(nextProps, nextState)</strong>:    .        false,    .</li>
                    <li><strong>render()</strong></li>
                    <li><strong>componentDidUpdate(prevProps, prevState)</strong>:       .        , ,         .</li>
                </ul>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
`class MyComponent extends React.Component {
    shouldComponentUpdate (nextProps, nextState) {
    //           name.
    //  false,     render  componentDidUpdate
    return nextProps.name !== this.props.name
  }

  componentDidUpdate (prevProps, prevState) {
    //       .
    console.log('  !')
  }

  render () {
    return <div>Hello, {this.props.name}!</div>
  }
}`
                        }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p>3. <strong>  (Unmounting Methods):</strong></p>
                <ul>
                    <li><strong>componentWillUnmount()</strong>:      DOM.     ,      .</li>
                </ul>
            </div>
            <div className="code-blok">
                        <pre>
                            <code>{
`class LifecycleComponent extends React.Component{
    constructor (props) {
        super(props)
          this.name = 'Alex'
     }   
    componentWillUnmount () {
        console.log('   DOM');
    }
    render(){
        console.log('render');
            return (
                <div>
                {this.props.age >= 18 ? <MyComponent /> : <p></p>} 
                //   MyComponent      "", 
                //   '   DOM'
                </div>
            )
    }
}   
//MyComponent.js
class MyComponent extends React.Component{
    componentWillUnmount () {
        console.log('   DOM');
    }
    render(){
            return (
                <p>!</p>
            )
    }
} `
                                }</code>
                        </pre>
            </div>
            <div className="text-blok">
                <p>4.<strong>Error Handling ( ):</strong></p>
                <ul>
                    <li><strong>componentDidCatch(error, info)</strong>:       .      .</li>
                </ul>
                <p>          <strong>useEffect</strong>.</p>
            </div>
        </>
    )
}
export default Lifecycle;