
const NavLinkComponent = () =>{

    return(
        <>
            <div className="text-blok">
                <p><strong>NavLink</strong></p>
                <p><strong>NavLink</strong>   <strong> </strong>,   "active"    .</p>
                <p> <strong>to</strong>   ,  <strong>activeClassName</strong>   "active"   .            .</p>
                <p>   <strong>Link  NavLink</strong>    <strong>  </strong>   NavLink. </p>
            </div>
            <div className="code-blok">
                <pre>
                    <code>{
                        `import { NavLink } from 'react-router-dom';

// // <Routes>...</Routes>...

<NavLink to="/" activeClassName="active"></NavLink>
<NavLink to="/about" activeClassName="active"> </NavLink>
<NavLink to="/contact" activeClassName="active"></NavLink>`
                    }</code>
                </pre>
            </div>
        </>

    )
}
export default NavLinkComponent;