import {useState} from 'react';

const State = () => {
    const [count, setCount] = useState(0);


    return(
        <>
            <div className='text-blok'>
                <h1>State(useState)</h1>
                <p><strong>State ()</strong>  React     JS,    .    .       ,    ,    ..</p>
                <p><strong>useState</strong>  . <strong></strong> (hook)   React    ,   ""   React   . </p>
                <p> React state        useState.</p>
            </div>
            <div className='code-blok'>
                <pre>
                    <code>{
`import React, { useState } from 'react';

const [state, setState] = useState(initialState);`
                    }</code>
                </pre>
            </div>
            <div className="text-blok">
                <p>   , React       (     setState    render()).</p> 
                <p> useState     : </p>
                <ul>
                    <li>  (state)</li>
                    <li>    (setState)</li>
                </ul>
                <p><strong>initialState</strong> ( ):      .</p>
                <p>   state  ,      .</p>
                <div className='counter'>
                    <button className='button-counter' onClick={() => setCount(count + 1)}>+1</button>
                    <p>Count: {count}</p>
                    <button className='button-counter' onClick={() => setCount(count - 1)}>-1</button>
                </div>
            </div>
            <div className='code-blok'>
                <pre>
                    <code>
{`import {useState} from 'react';
const Counter = () => {
    const [count, setCount] = useState(0);
    return(
        <div className='counter'>
            <button className='button-counter' onClick={() => setCount(count + 1)}>+1</button>
            <p>Count: {count}</p>
            <button className='button-counter' onClick={() => setCount(count - 1)}>-1</button>
        </div>
    )
}
export default Counter;`
                    }</code>
                </pre>
            </div>
            <div className='text-blok'>
                <p>    . State    .  state    setState.</p>
            </div>
            <div className='code-blok'>
                <pre>
                    <code>{
`class Example extends React.Component {
    constructor(props) {
        super(props)
        this.state = { count: 0 }
    }

    handleClick = () => {
        this.setState((state) => ({ count: state.count + 1 }));   
        // callback ,      
        //this.setState({ count: this.state.count + 1 }); 
        // ,  
    }

    render() {
        return (
            <div>
                <p>  {this.state.count} </p>
                <button onClick={this.handleClick}> </button>
            </div>
        );
    }
}`
                        }</code>
                </pre>
            </div>
        </>
    )
}
export default State;